* {
    font-family: "Roboto Mono", sans-sarif;
    font-size: 18px;
  }

  body {
    display: flex;
    justify-content: center;
    align-items: center;
  }
    
  .container {
    background-color: #15173c;
    position: absolute;
    padding: 20px 20px;
    transform: translate(-50%, -50%);
    margin:0 auto;
    width:256px;
    top: 50%;
    left: 50%;
    text-align: center;
    border: 10px solid #171947;
    background: #15173c;
    border-radius:5px;
  }
    
  .keypad {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-auto-rows: minmax(60px,auto);
    
  }

  input[type="text"] {
    height:75px;
    width:249px;
    background-color:#171947;  ;
    border:0px;
    color:rgb(164, 207, 244);
    text-align: right;
    font-size: 25px;
    font-weight:500;
    letter-spacing: 1px;
  }

  #clear {
    grid-column: 1/3;
    grid-row: 1;
    color: #360705;;
  }

  #result {
    grid-column:  3/5;
    grid-row: 5;
    color: #d64540;
  }
    
  #backspace {
    color: #360705;
  }
    
 #button {
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    border: none;
    background-color:#360604; 
    color: #6a0e0b;
    font-weight: 500;
 }
 input:focus,
 input:active,
 button:focus,
 button:active, 

  
 .highlight{
    background-color: #f96760;
 }
